<template>
  <div>
    <Row>
      <Col span="24">
        <div>
          <div class="depot">
            <div class="in">
              <div class="member">
                <span></span>
              </div>
            </div>
          </div>
          <div class="depot-table">
            <Table :columns="columns" :data="data">
              <template slot-scope="{ row, index }" slot="name">
                <Input
                  type="text"
                  v-model.trim="editName"
                  v-if="editIndex === index"
                  placeholder=""
                />
                <span v-else>{{ row.name }}</span>
              </template>
              <template slot-scope="{ row, index }" slot="age">
                <Input
                  type="text"
                  v-model.trim="editAge"
                  v-if="editIndex === index"
                  placeholder=""
                />
                <span v-else>{{ row.age }}</span>
              </template>
              <template slot-scope="{ row, index }" slot="address">
                <Input
                  type="text"
                  v-model.trim="editAddress"
                  v-if="editIndex === index"
                  placeholder=""
                />
                <span v-else>{{ row.address }}</span>
              </template>
              <template slot-scope="{ row, index }" slot="action">
                <div v-if="editIndex === index">
                  <a href="javascript:;" @click="handleSave(index)"></a>
                  <a href="javascript:;" style="margin-left:5px"></a>
                </div>
                <div v-else>
                  <a href="javascript:;" @click="handleEdit(row, index)"></a>
                  <Poptip confirm title="" @on-ok="ok(index)" placement="right">
                    <a href="javascript:;" style="margin-left:5px"></a>
                  </Poptip>
                </div>
              </template>
            </Table>
            <Row>
              <Col span="24">
                <div style="padding-top:20px;">
                  <Button type="primary" ghost long @click="additem()">
                    <Icon type="ios-add" size="20" />
                  </Button>
                </div>
              </Col>
            </Row>
          </div>
        </div>
      </Col>
    </Row>
  </div>
</template>;

<script>
export default {
  data() {
    return {
      columns: [
        {
          title: "",
          slot: "name"
        },
        {
          title: "",
          slot: "age"
        },

        {
          title: "",
          slot: "address"
        },
        {
          title: "",
          slot: "action"
        }
      ],
      data: [
        {
          name: "",
          age: 18,
          address: ""
        },
        {
          name: "",
          age: 25,
          address: ""
        },
        {
          name: "",
          age: 30,
          address: ""
        },
        {
          name: "",
          age: 26,
          address: ""
        }
      ],
      editIndex: -1, // 
      editName: "", //  data 
      editAge: "", // 
      editAddress: "" // 
    };
  },
  methods: {
    handleEdit(row, index) {
      this.editName = row.name;
      this.editAge = row.age;
      this.editAddress = row.address;
      this.editIndex = index;
    },
    handleSave(index) {
      this.data[index].name = this.editName;
      this.data[index].age = this.editAge;
      this.data[index].address = this.editAddress;
      if (this.editName == null || this.editName == "") {
        this.$Message.error("");
      } else if (this.editAge == null || this.editAge == "") {
        this.$Message.error("");
      } else if (this.editAddress == null || this.editAddress == "") {
        this.$Message.error("");
      } else {
        this.editIndex = -1;
      }
    },
    ok(index) {
      this.data.splice(index, 1);
    },

    additem() {
      let obj = {
        name: null,
        age: null,
        address: null
      };

      this.editName = obj.name;
      this.editAge = obj.age;
      this.editAddress = obj.address;
      this.data.push(obj);
      this.editIndex = this.data.length - 1;
    }
  }
};
</script>

<style scoped>
.depot .member {
  font-size: 16px;
  background: #fff;
  padding: 20px 0 20px 20px;
  border-bottom: 1px solid #ccc;
}

.depot-table {
  padding: 23px;
  background: #fff;
}
</style>